  /*  1024px */
  @media (min-width: 1024px) {
    .custom-profile-body {
        flex-direction: row;
    }

    .custom-container {
        flex-direction: row;
      }
    
      .custom-sidebar {
        width: 25%;
      }
    
      .custom-content {
        width: 65%;
      }
  }
  
